// portfolio
.portfolio-section {
    padding: 20px 0;
	min-height: 100vh;
	::-webkit-scrollbar {
		height: 0;
	}
	&__content {
		position: relative;
		justify-content: space-between;
		width: 100%;
		background-color: transparent;
        height: 70vh;
		// grid
		&--grid {
			justify-content: space-between;
			width: 100%;
			height: 100%;
			padding-bottom: 10px;
			overflow-x: scroll;
		}
		.grid-nav {
			position: absolute;
			display: flex;
			top: -44px;
			left: 50%;
			transform: translateX(-50%);
			@include mobile-breakpoint {
				width: 100%;
			}
			li {
				position: relative;
				font-size: 18px;
				text-transform: uppercase;
				font-weight: 600;
				margin: 0 22px;
				color: $bl-secondary;
				cursor: pointer;
				@include mobile-breakpoint {
					font-size: 14px;
					font-weight: 400;
				}
			}
			.active {
				&::after {
					position: absolute;
                    content: "";
                    top: 20px;
                    left: -11%;
                    width: 120%;
                    height: 3px;
                    border-radius: 3px;
                    background-color: rgba($bl-secondary, 1);
                    z-index: -1;
                    transition: $short-animation;
                    transform-origin: center;
                    @include mobile-breakpoint {
                        height: 2px;
                    }
				}
			}
		}
		.illustration-portfolio-grid {
			grid-template-rows: minmax(0, 1fr) minmax(0, 1fr) minmax(0, 1fr);
			grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
			width: 4696px;
		}
		.storyboard-portfolio-grid {
			grid-template-columns: repeat(8, 1fr);
			grid-template-rows: repeat(3, 1fr);
		}
		.motiondesign-portfolio-grid {
			grid-template-columns: repeat(2, 1fr);
			grid-template-rows: repeat(2, 1fr);
		}
		.div1 { grid-area: 1 / 1 / 2 / 3; }
		.div2 { grid-area: 2 / 1 / 3 / 3; }
		.div3 { grid-area: 3 / 1 / 4 / 3; }
		.div4 { grid-area: 1 / 3 / 3 / 5; }
		.div5 { grid-area: 3 / 3 / 4 / 5; }
		.div6 { grid-area: 2 / 5 / 4 / 7; }
		.div7 { grid-area: 1 / 5 / 2 / 7; }
		.div8 { grid-area: 1 / 7 / 2 / 9; }
		.div9 { grid-area: 2 / 7 / 3 / 9; }
		.div10 { grid-area: 3 / 7 / 4 / 9; }

		
		.item-33 { grid-area: 1 / 1 / 3 / 2; }
		.item-34 { grid-area: 1 / 2 / 3 / 3; }

		.portfolio-grid {
			display: grid;
			overflow-x: hidden;
			gap: 5px;
			height: 100%;
			.grid-item {
				width: 100%;
				height: 100%;
				overflow: hidden;
				animation: pictures-fade-in 2000ms cubic-bezier(0.075, 0.82, 0.165, 1) forwards;
				opacity: 0;
				.portoflio-illustration {
					width: 100%;
					height: 100%;
					object-fit: cover;
					cursor: pointer;
					transition: $short-animation;
					&:hover {
						// transform: scale(1.05);
						filter: brightness(0.75);
					}
				}
			}
		}
		#item-0 {
			grid-row-start: 1;
			grid-column-start: 1;

			grid-row-end: 2;
			grid-column-end: 2;
		}
		#item-1 {
			grid-row-start: 1;
			grid-column-start: 3;

			grid-row-end: 3;
			grid-column-end: 4;
		}
		#item-2 {
			grid-row-start: 2;
			grid-column-start: 1;

			grid-row-end: 3;
			grid-column-end: 2;
		}
		#item-3 {
			grid-row-start: 1;
