		}
		#item-21 {
			grid-row-start: 2;
			grid-column-start: 15;
			grid-row-end: 4;
			grid-column-end: 17;
		}
		#item-22 {
			grid-row-start: 1;
			grid-column-start: 15;
			grid-row-end: 2;
			grid-column-end: 15;
		}
		#item-23 {
			grid-row-start: 1;
			grid-column-start: 16;
			grid-row-end: 2;
			grid-column-end: 16;
		}



		#item-33 {
			grid-row-start: 1;
			grid-column-start: 27;
			grid-row-end: 4;
			grid-column-end: 30;
		}
		#item-34 {
			grid-row-start: 1;
			grid-column-start: 30;
			grid-row-end: 4;
			grid-column-end: 33;
		}
	}
	// grid button
	.slide-button {
		position: absolute;
		bottom: 10%;
        padding: 0 10px;
		background-color: transparent;
		color: white;
		font-size: 2.8em;
		font-weight: 100;
		cursor: pointer;
        border-radius: 4px;
		transition: $long-animation;
		transform: scaleX(2);
        background-color: rgba(255,255,255,0.4);
		@include tablet-breakpoint {
			font-size: 2em;
		}
		@include mobile-breakpoint {
			display: none;
			}
	}
	.slide-right {
		right: 15%;
		opacity: 0;
		animation: grid-btn 1000ms ease-in-out forwards 300ms;
		&:hover {
			transform: translateX(10px) scaleX(2);
		}
	}
	.slide-left {
		left: 15%;
		opacity: 0;
		animation: grid-btn 1000ms ease-in-out forwards 500ms;
		&:hover {
			transform: translateX(-10px) scaleX(2);
		}
	}
}

@keyframes pictures-fade-in {
	0% {
		opacity: 0;
		transform: translateX(30%);
	}
	100% {
		opacity: 1;
		transform: translateY(0);
	}
}

@keyframes grid-btn {
	0% {
		opacity: 0;
	}
	100% {
		opacity: 1;
	}
}


// full pics screen
.full-screen-container {
	position: fixed;
	display: flex;
	justify-content: center;
	align-items: center;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	z-index: 1000;
	cursor: pointer;
	transform: translateX(0);
	animation: open-full-screen 500ms cubic-bezier(0.66, 0, 0.3, 0.98);
	&__content {
		position: relative;
		width: 80%;
		height: 80%;
	}
	img {
		width: 100%;
		height: 100%;
		object-fit: contain;
		animation: img-appaer 500ms cubic-bezier(0.66, 0, 0.3, 0.98) 100ms;
	}
}

@keyframes open-full-screen {
	from {
		transform: translateX(-100%);
		opacity: 0;
	}
	to {
		transform: translateX(0);
		opacity: 1;
	}
}
@keyframes close-full-screen {
	from {
		transform: translateX(0);
		opacity: 1;
	}
	to {
		transform: translateX(-100%);
		opacity: 0;
	}
}

@keyframes img-appaer {
	from {
		opacity: 0;
		transform: translateX(-100%);
	}
	to {
		opacity: 1;
		transform: translateX(0);
	}
}
@keyframes img-disappaer {
	from {
		opacity: 1;
		transform: translateX(0);
	}
	to {
		opacity: 0;
		transform: translateX(100%);
	}
}
